.blog {
  display: grid;
  grid-template-areas: "nav center content";
  grid-template-rows: 100vh;
  grid-template-columns: auto auto 65vw;
}
.blog-navigation {
  background-color: white;
  grid-area: nav;
  display: grid;
  -webkit-transition: width 0.8s ease-in-out;
  -o-transition: width 0.8s ease-in-out;
  transition: width 0.8s ease-in-out;
}
.blog-navigation h2 {
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
  margin: 1em 0 1em 0;
}
.blog-navigation-accordions {
  width: 15vw;
  height: 60vh;
  overflow-x: hidden;
  margin: 20vh 5vw 20vh 5vw;
  display: grid;
  grid-template-rows: 120px 1fr;
}
.blog-navigation-accordions #blog-link {
  height: auto;
  -ms-flex-pack: left;
  justify-content: left;
}
.blog-navigation-latest,
.blog-navigation-category {
  width: 100%;
}
.blog-navigation#hide {
  width: 25vw;
}
.blog-navigation#show {
  width: 0vw;
}
.blog-hero {
  background-color: black;
  padding: 5vw;
  position: relative;
  grid-area: center;
  display: grid;
  -ms-flex-align: center;
  align-items: center;
  -webkit-transition: width 0.8s ease-in-out;
  -o-transition: width 0.8s ease-in-out;
  transition: width 0.8s ease-in-out;
}
.blog-hero-content {
  max-width: 35vw;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr;
  -ms-flex-align: end;
  align-items: flex-end;
}
.blog-hero-content h1 {
  font-family: "Rubik Mono One", sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
}
.blog-hero-content * {
  color: white;
  margin: 2.5% 0;
}
.blog-hero#hide {
  width: 65vw;
}
.blog-hero#show {
  width: 25vw;
}
.blog-post {
  background-color: white;
  padding: 5%;
  position: relative;
  z-index: 2;
  grid-area: content;
}
.blog-post-content {
  height: 100%;
  overflow-y: auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 2.5vw;
}
.blog-post-content-section {
  display: grid;
  grid-template-areas: "heading image" "text image";
  grid-template-rows: auto
          1fr;
  grid-template-columns: 1fr 1fr;
}
.blog-post-content-section-heading {
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
  grid-area: heading;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
  align-items: flex-end;
}
.blog-post-content-section-text {
  text-align: justify;
  padding: 5% 5% 5% 0;
  grid-area: text;
}
.blog-post-content-section-image {
  grid-area: image;
}
.blog-post-content-section-image img {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.blog-post #posts-open {
  background-color: white;
  width: 176px;
  position: absolute;
  bottom: 48px;
  left: -176px;
}
.blog-post #posts-close {
  background-color: white;
  position: absolute;
  bottom: 48px;
  left: -48px;
}
@-webkit-keyframes fade {
  0% {
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
}



