.btn {
  white-space: pre;
  height: 48px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.btn:hover {
  color: black;
  cursor: pointer;
}
.btn-square {
  width: 48px;
  -ms-flex-pack: center;
  justify-content: center;
}
.btn-panel {
  padding: 0 1em 0 1em;
  -ms-flex-pack: center;
  justify-content: center;
}
.btn-rectangle {
  width: auto;
  padding: 0 1em 0 1em;
}



